/*Cogs*/

.spoke {
  background-color: #426E86;
  position: absolute;
  height: 175px;
  width: 30px;
  margin-left: -15px;
  margin-top: -1px;
  left: 50%;
  border-radius: 5px;
  z-index: 10;
}

.spokeUnlock {
  background-color: #426E86;
  position: absolute;
  height: 175px;
  width: 30px;
  margin-left: -15px;
  margin-top: -1px;
  left: 50%;
  border-radius: 5px;
  z-index: 10;
}

.spoke + .spoke {
  transform: rotateZ(30deg);
  -ms-transform: rotateZ(30deg);
  -webkit-transform: rotateZ(30deg);
}

.spoke + .spoke + .spoke {
  transform: rotateZ(60deg);
  -ms-transform: rotateZ(60deg);
  -webkit-transform: rotateZ(60deg);
}

.spoke + .spoke + .spoke +.spoke {
  transform: rotateZ(90deg);
  -ms-transform: rotateZ(90deg);
  -webkit-transform: rotateZ(90deg);
}

.spoke + .spoke + .spoke + .spoke + .spoke {
  transform: rotateZ(120deg);
  -ms-transform: rotateZ(120deg);
  -webkit-transform: rotateZ(120deg);
}

.spoke + .spoke + .spoke + .spoke + .spoke + .spoke {
  transform: rotateZ(150deg);
  -ms-transform: rotateZ(150deg);
  -webkit-transform: rotateZ(150deg);
}

.spokeUnlock + .spokeUnlock {
  transform: rotateZ(30deg);
  -ms-transform: rotateZ(30deg);
  -webkit-transform: rotateZ(30deg);
}

.spokeUnlock + .spokeUnlock + .spokeUnlock {
  transform: rotateZ(60deg);
  -ms-transform: rotateZ(60deg);
  -webkit-transform: rotateZ(60deg);
}

.spokeUnlock + .spokeUnlock + .spokeUnlock +.spokeUnlock {
  transform: rotateZ(90deg);
  -ms-transform: rotateZ(90deg);
  -webkit-transform: rotateZ(90deg);
}

.spokeUnlock + .spokeUnlock + .spokeUnlock + .spokeUnlock + .spokeUnlock {
  transform: rotateZ(120deg);
  -ms-transform: rotateZ(120deg);
  -webkit-transform: rotateZ(120deg);
}

.spokeUnlock + .spokeUnlock + .spokeUnlock + .spokeUnlock + .spokeUnlock + .spokeUnlock {
  transform: rotateZ(150deg);
  -ms-transform: rotateZ(150deg);
  -webkit-transform: rotateZ(150deg);
}

