  transition-duration: 0.3s;
}
.hvr-shutter-out-verticalBlue:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #7f7f7f;
  opacity: 0.9;
  color: white;
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-shutter-out-verticalBlue:hover, .hvr-shutter-out-verticalBlue:focus, .hvr-shutter-out-verticalBlue:active {
  color: white;
}
.hvr-shutter-out-verticalBlue:hover:before, .hvr-shutter-out-verticalBlue:focus:before, .hvr-shutter-out-verticalBlue:active:before {
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
}


